.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: beige;
}

.App-link {
  color: #61dafb;
}
* {
	text-align: center;
}

.logo {
	margin: auto
}

.jumbotron {
	margin: 100px auto;
	max-width: 50%;
	text-align: center;
}

.card {
	width: 18rem;
	margin: 100px auto;
}
